import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MailService } from '../../services/mail.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  constructor( private snackBar : MatSnackBar,
               private mail: MailService ) { }

  ngOnInit(): void {
  }

  goToLinkedIn = () => window.open('https://www.linkedin.com/in/santiago-garat-2b910a1a2/', "_blank");
  goToWhatsApp = () => window.open('https://api.whatsapp.com/send?phone=5491156635461', "_blank");
  goToInstagram = () => window.open('https://www.instagram.com/garat1989/', "_blank");

  user = {
    name: '',
    tel: '',
    mail: '',
    text: ''
  }

  save( form : NgForm ){
    let formData={
      name: form.value.name,
      mail: form.value.mail,
      phone: form.value.tel,
      message: form.value.text,
    }
    if( form.invalid ){ Object.values( form.controls ).forEach( value => value.markAllAsTouched() ) };
    if( !form.invalid ) {
      this.mail.sendMail(formData).subscribe(
        (resp)=>{
            console.log(resp);
            form.reset();
            this.snackBar.open('Mensaje Enviado', 'Aceptar', { duration: 3000 });
          },(error)=>{
          console.log(error);
          this.snackBar.open('Oops! Algo salió mal', 'Aceptar', { duration: 3000 });
        }
      )
    };
  }

}
